
.screen-loader-container {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    background-color: #f3f3f3;
}

.screen-loader {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    transform: scale(2);
}
.screen-loader div {
    animation: loading 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
}
.screen-loader div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #FFD800;
    margin: -4px 0 0 -4px;
}
.screen-loader div:nth-child(1) {
    animation-delay: -0.036s;
}
.screen-loader div:nth-child(1):after {
    top: 63px;
    left: 63px;
}
.screen-loader div:nth-child(2) {
    animation-delay: -0.072s;
}
.screen-loader div:nth-child(2):after {
    top: 68px;
    left: 56px;
}
.screen-loader div:nth-child(3) {
    animation-delay: -0.108s;
}
.screen-loader div:nth-child(3):after {
    top: 71px;
    left: 48px;
}
.screen-loader div:nth-child(4) {
    animation-delay: -0.144s;
}
.screen-loader div:nth-child(4):after {
    top: 72px;
    left: 40px;
}
.screen-loader div:nth-child(5) {
    animation-delay: -0.18s;
}
.screen-loader div:nth-child(5):after {
    top: 71px;
    left: 32px;
}
.screen-loader div:nth-child(6) {
    animation-delay: -0.216s;
}
.screen-loader div:nth-child(6):after {
    top: 68px;
    left: 24px;
}
.screen-loader div:nth-child(7) {
    animation-delay: -0.252s;
}
.screen-loader div:nth-child(7):after {
    top: 63px;
    left: 17px;
}
.screen-loader div:nth-child(8) {
    animation-delay: -0.288s;
}
.screen-loader div:nth-child(8):after {
    top: 56px;
    left: 12px;
}
@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
  
